* {
  box-sizing: border-box;
}

.matrix_wrapper {
  position: relative;
  height: 750px;
  width: 900px;
  margin-bottom: 100px;
  margin-left: 0px;
  //background: radial-gradient(circle, rgba(226,225,224,1) 0%, rgba(255,255,255,1) 100%);
}

.bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 1;
}

@media screen and (max-width: 1525px) {
  .matrix_wrapper {
    margin-bottom: 20px;
  }
}


