
@media screen and (max-width: 1120px) {

  * {
    box-sizing: border-box;
  }

  .matrix-compatibility {
    position: relative;
    height: 590px;
    margin-bottom: 100px;
    border: 1px solid rgba(255, 99, 71, 0);
    overflow-x: hidden;
    overflow-y: visible;
    margin-left: 0%;


    .purposes {
      position: absolute;
      bottom: 0;
      width: 100%;
      margin-left: 12%;
      //border: 1px solid red;

      h3 {
        font-family: 'Playfair Display', serif;
        margin: 0;
        color: black;
      }

      .purposes_top {
        //border: 1px solid yellowgreen;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 10px 0;
        gap: 5px;

        .purpose {
          //border: 1px solid blue;
          .circle_purpose {
            width: 30px;
            height: 30px;
          }
        }

      }

      .purpose_general {
        //border: 1px solid blue;
        height: 80px;
        margin-top: -5px;
        margin-bottom: -20px;

        .circle_solo {
          .circle {
            display: flex;
            justify-content: center;
            align-items: flex-start;
            width: 30px;
            height: 30px;
            background-color: white;
            font-size: 19px;
            border: none;
            color: black;
          }

        }
        #circle_soul {
          background-color: white;
          color: black;
          border: 1px solid black;
        }
      }
    }
  }

  @media screen and (max-width: 1525px) {
    .matrix_wrapper {
      margin-bottom: 20px;
    }
  }

  .matrix-appointment {
    position: absolute;
    bottom: 0;
    width: 100%;

    h3 {
      color: #3a3a3a;
    }

    .life-map_wrapper {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      gap: 10px;

      .life-map_item {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 10px;
        width: 270px;
        margin-top: 5px;

        .life-map_item-circle {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 30px;
          height: 30px;
          border: 1px solid black;
          border-radius: 50%;
          font-weight: bold;
        }

        .circle-bg {
          border: none;
          background-color: $violet-dark;
          color: black;
        }

        .circle-bg-dark {
          border: none;
          background-color: $violet-dark;
          color: black;
        }

        .life-map_item-title {
          font-family: 'Playfair Display', serif;
          font-size: 13px;
          color: black;
        }
      }
    }
  }
}

