      position: absolute;
      padding-left: 1px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: normal;
      font-size: 4.3px;
      width: 38px;
      color: rgba(0, 0, 0, 0.89);
      left: 4px;
      top: -1px;
      letter-spacing: 0.3px;
      font-family: 'Playfair Display', serif;
    }

