  .matrix-appointment {
    h3 {
      font-family: 'Playfair Display', serif;
      color: #3a3a3a;
      font-size: 12px;
      margin-bottom: 0px;
    }

    .life-map_wrapper {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      gap: 5px;


      .life-map_items {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 5px;

        .life-map_item {

          display: flex;
          flex-direction: row;

          gap: 5px;
          width: 175px;
          margin-top: 5px;

          .life-map_item-circle {
            display: flex;
            justify-content: center;
            align-items: flex-start;
            width: 25px;
            height: 25px;
            margin-left: 25px;
            border: 1px solid #114257;
            border-radius: 50%;
            font-weight: bold;
            font-size: 12px;
          }

          .circle-bg {
            border: none;
            background-color: $violet-dark;
            color: white;
          }

          .circle-bg-dark {
            border: none;
            background-color: $violet-dark;
            color: white;
          }

          .life-map_item-title {
            font-size: 10px;
            color: #114257;
          }
        }
      }
    }


  }
