$violet: #8847A3;
$blue-dark: #526DFC;
$blue-light: #47CFDD;
$green: #00BF63;
$yellow: #F8ED07;
$orange: #FF914D;
$red: #E32201;

.seven-generations-table {
  display: flex;
  flex-direction: column;
  gap: 25px;
  margin: 35px auto;
  @media screen and (max-width: 800px) {
    margin: 15px auto;
  }


  .seven-generations-table_father, .seven-generations-table_mather {
    .seven-generations-table_h2 {
      font-family: 'Playfair Display', serif;
      font-size: 19px;
      @media screen and (max-width: 800px) {
        font-size: 16px;
      }
    }

    .seven-generations-table_row {
      //border: 1px solid red;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      width: 500px;
      @media screen and (max-width: 800px) {
        gap: 5px;
        width: 100%;
      }

      .seven-generations-table_row_circle {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: flex-start;
        font-family: 'Playfair Display', serif;
        font-size: 28px;
        width: 50px;
        height: 50px;
        border: 1px solid #3a3a3a;
        border-radius: 50%;
        color: black;
        @media screen and (max-width: 800px) {
          width: 40px;
          height: 40px;
          font-size: 23px;
        }
      }

      .violet {
        background-color: $violet;
      }

      .blue-dark {
        background-color: $blue-dark;
      }

      .blue-light {
        background-color: $blue-light;
      }

      .green {
        background-color: $green;
      }

      .yellow {
        background-color: $yellow;
      }

      .orange {
        background-color: $orange;
      }

      .red {
        background-color: $red;
      }

    }
  }
}